import { createUserWithEmailAndPassword } from "firebase/auth";
import { auth } from "../firebase";
import { useNavigate } from "react-router";


export const Register = (props) => {
    const navigate = useNavigate();
     const handleSubmit = async (e) => {
        e.preventDefault();
        const { email, password } = e.target.elements;
            await createUserWithEmailAndPassword(auth, email?.value, password?.value)
        .then((userCredential) => {
            // Signed in
            const user = userCredential.user;
            console.log(user);
            navigate("/")
            // ...
        })
        .catch((error) => {
            const errorCode = error.code;
            const errorMessage = error.message;
            console.log(errorCode, errorMessage);
            // ..
        });
    }
    return (
        <>
        <section className="sign-in-form section-padding">
                <div className="container">
                    <div className="row">

                        <div className="col-lg-8 mx-auto col-12">

                            <h1 className="hero-title text-center mb-5">Register</h1>

                            <div className="row">
                                <div className="col-lg-8 col-11 mx-auto">
                                    <form role="form" method="post" onSubmit={handleSubmit}>

                                        <div className="form-floating mb-4 p-0">
                                            <input type="email" name="email" id="email" pattern="[^ @]*@[^ @]*" className="form-control" placeholder="Email address" required />

                                            <label for="email">Email address</label>
                                        </div>

                                        <div className="form-floating p-0">
                                            <input type="password" name="password" id="password" className="form-control" placeholder="Password" required />

                                            <label for="password">Password</label>
                                        </div>

                                        {/* <div className="form-floating mt-4 p-0">
                                            <input type="password" name="confirmPassword" id="confirmPassword" className="form-control" placeholder="Confirm Password" required />

                                            <label for="password">Confirm Password</label>
                                        </div> */}

                                        <button  className="btn custom-btn form-control mt-4 mb-3" type="submit">        
                                            Register
                                        </button>

                                    </form>
                                </div>
                            </div>
                            
                        </div>

                    </div>
                </div>
            </section>
        </>
    )
}

export default Register;